@import url('https://fonts.googleapis.com/css2?family=Varela+Round&display=swap');
*{
  font-family: 'Varela Round', sans-serif;
}
a{
  text-decoration: none !important;
}
h1,h2,h3,h4,h5{
  font-weight: 800;
}
.alt-box{
  padding: 50px 0px;
  background: #264d64;
}
.alt-box h1, .alt-box p{
  color: #FFF;
}
.mb-n70{
  margin-bottom: -70px;
}
.ptb-50{
  padding: 50px 0px;
}
.alt-box h1{
  font-size: 50px;
  font-weight: 800;
}
.alt-title h2{
  border-left: 5px solid #FF8C32;
  padding-left: 15px;
  font-size: 26px;
}
.btn-main{
  background: #264d64;
  color: #FFF;
  padding: 8px;
  font-size: 20px;
}
.mobile-form select{
  padding: 12px;
  margin-bottom: 20px;
}
.card:hover{
  box-shadow: 0 0 10px 0 rgb(0 0 0 / 11%);
}
.card-body{
  width: 80% !important;
  margin: 0px auto;
  padding: 15px 0px;
  text-align: center;
}
.card-body img{
  width: auto;
  height: 100px;
}
.card-body p{
  margin-bottom: 0px;
  margin-top: 5px;
  font-weight: 700;
  color:black;
  text-transform: capitalize;
  font-size: 14px;
}
.bg-grey{
  background: #EEEEEE;
}
.work-box {
  padding: 20px;
  text-align: center;
  background: white;
  border-radius: 10px;
  height: 100%;
}
.work-boximg img {
  height: 100px;
  margin-bottom: 15px;
  margin-top: 10px;
}
.form-select:focus {
  border-color: black;
  box-shadow: none !important;
}
.btn-main{
  margin-right: 10px;
      background: #FFF !important;
      font-weight: 600;
      font-size: 16px !important;
      padding: 8px 20px;
      border: 1px solid black;
      color:black !important;
}